section#articles {
  grid-template-columns: repeat(4, 1fr);
  margin: 20px 15px;
  display: grid;
}

article.news {
  border: 1px solid #756e6e;
  border-radius: 4px;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
  margin-right: 15px;
  padding: 10px;
  display: flex;
}

article.news img {
  max-width: 300px;
}

.news-link {
  color: wheat;
  background: #d2691e;
  border-radius: 4px;
  margin-top: 10px;
  padding: 10px 25px;
  text-decoration: none;
}

